:root {
  --font-body: Helvetica, sans-serif;
}

:root {
  --lavender-white: hsl(249, 33%, 96%);
  --lavender-lighter: hsl(247, 35%, 80%);
  --lavender-light: hsl(246, 35%, 62%);
  --lavender: hsl(248, 35%, 40%);
  --lavender-dark: hsl(246, 35%, 24%);
  --lavender-darker: hsl(240, 31%, 16%);
  --lavender-bg: hsla(240, 32%, 8%, 0.075);
  --lavender-black: hsl(240, 32%, 8%);
  --website: hsl(151, 100%, 44%);
  --github-light: hsl(213, 13%, 16%);
  --github-dark: hsl(210, 67%, 96%);
  --linkedin: hsl(210, 90%, 40%);
  --transition: all 150ms;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  color: var(--lavender-black);
}
@media (prefers-color-scheme: light) {
  body {
    background-color: var(--lavender-bg);
  }
}
@media (prefers-color-scheme: dark) {
  body {
    background-color: var(--lavender-darker);
  }
}

html {
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.nav {
  visibility: hidden;
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
}
.nav__toggle {
  border: none;
  border-radius: 1rem;
  padding: 0.75rem 1.5rem;
}
@media (prefers-color-scheme: light) {
  .nav__toggle {
    background-color: var(--lavender-light);
    border: 0.125rem solid var(--lavender-lighter);
  }
}
@media (prefers-color-scheme: dark) {
  .nav__toggle {
    background-color: var(--lavender);
    border: 0.125rem solid var(--lavender-light);
  }
}
.nav__toggle:hover {
  cursor: pointer;
  transition: var(--transition);
}
@media (prefers-color-scheme: light) {
  .nav__toggle:hover {
    background-color: var(--lavender);
  }
}
@media (prefers-color-scheme: dark) {
  .nav__toggle:hover {
    background-color: var(--lavender-dark);
  }
}
.nav__toggle--icon {
  font-size: 1.125rem;
  color: var(--lavender-white);
}

.main {
  text-align: center;
  padding: 0 4.0625rem;
}
@media (min-width: 40em) {
  .main {
    text-align: justify;
    max-width: 50rem;
    padding: 3.75rem 5rem;
    margin: 0 auto;
    border-radius: 1rem;
  }
}
@media (min-width: 40em) and (prefers-color-scheme: light) {
  .main {
    background-color: var(--lavender-white);
    border: 0.125rem solid var(--lavender-lighter);
  }
}
@media (min-width: 40em) and (prefers-color-scheme: dark) {
  .main {
    background-color: var(--lavender-dark);
    border: 0.125rem solid var(--lavender-light);
  }
}
.main__title {
  margin-bottom: 2.25rem;
  color: var(--lavender-light);
}
@media (min-width: 40em) {
  .main__title {
    font-size: 3.125rem;
    margin-bottom: 1.25rem;
  }
}
.main__about-me {
  font-size: 1rem;
  line-height: 1.5rem;
  margin-bottom: 1rem;
}
@media (min-width: 40em) {
  .main__about-me {
    font-size: 1.25rem;
  }
}
@media (prefers-color-scheme: dark) {
  .main__about-me {
    color: var(--lavender-lighter);
  }
}
@media (prefers-color-scheme: light) {
  .main__about-me--anchor {
    color: var(--lavender-light);
  }
}
@media (prefers-color-scheme: dark) {
  .main__about-me--anchor {
    color: var(--lavender-light);
  }
}
.main__about-me--anchor:hover {
  border-bottom: 0.0625rem dashed var(--lavender-lighter);
  transition: var(--transition);
}
@media (prefers-color-scheme: light) {
  .main__about-me--anchor:hover {
    color: var(--lavender);
  }
}
@media (prefers-color-scheme: dark) {
  .main__about-me--anchor:hover {
    color: var(--lavender-lighter);
  }
}
.main__name {
  font-size: 0.875rem;
  text-transform: capitalize;
}
@media (prefers-color-scheme: light) {
  .main__name {
    color: var(--lavender-darker);
  }
}
@media (prefers-color-scheme: dark) {
  .main__name {
    color: var(--lavender-light);
  }
}

.footer {
  padding-bottom: 1rem;
}
.footer__socials {
  margin-bottom: 0.625rem;
}
.footer__socials--ul {
  display: flex;
  justify-content: center;
  gap: 0.875rem;
  list-style: none;
}
.footer__paragraph {
  text-align: center;
  text-transform: capitalize;
  font-size: 0.75rem;
}
@media (prefers-color-scheme: light) {
  .footer__paragraph {
    color: var(--lavender-dark);
  }
}
@media (prefers-color-scheme: dark) {
  .footer__paragraph {
    color: var(--lavender-lighter);
  }
}

.icon {
  font-size: 1.25rem;
  color: var(--lavender-light);
}
.icon-website:hover {
  color: var(--website);
}
.icon-github:hover {
  color: var(--github-light);
}
@media (prefers-color-scheme: dark) {
  .icon-github:hover {
    color: var(--github-dark);
  }
}
.icon-linkedin:hover {
  color: var(--linkedin);
}
.icon:hover {
  transition: var(--transition);
}